html {
    box-sizing: border-box;
    font-size: 100%;
    overflow-x: hidden;
    width: 100%;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    background-color: $white;
    color: $black;
    font-family: 'century-gothic', sans-serif;
    margin: 0;
    padding: 0;
}

ul li {
    list-style-type: none;
}

a {
    color: $black;
    text-decoration: none;
}



// UI elements
.line {
    background-color: $accent;
    width: 75px;
    height: 2px;
    opacity: .7;
}

.container {
    max-width: 1140px;
    padding: 0 2%;
    margin: 4rem auto 0;
}

.space {
    height: 250px;
    width: 100%;
}


// Sub-Heading
.sub-heading {
    padding: 8rem 6% 3rem;
    margin: auto;
}

.parent {
    position: relative;
}