.web-dev {
  display: flex;
  justify-content: space-between;

  img {
    filter: grayscale(100%);
    transition: all .4s;
    max-width: 60px;
  }
}



.web-dev img:hover {
  filter: grayscale(0%);
  transition: all;
}

@media #{$mq-760-down} {
  .web-dev {
    flex-wrap: wrap;
    justify-content: center;

    img {
      margin-right: 1rem;
    }
  }
}