.info {
  display: flex;
  align-items: center;
  justify-content: space-around;

  img {
    width: 100%;
    max-width: 400px;
    height: auto;
  }

  p {
    max-width: 385px;
    line-height: 2.2;
  }
}

.botton-filler {
  position: absolute;
  bottom: 100px;
  left: 0;
  z-index: -99;
}


@media #{$mq-760-down} {
  .botton-filler {
    display: none;
  }

  .info {
    display: block;
  }
}