.work-item {
  display: grid;
  grid-template-columns: auto auto;
  margin: 2rem auto;

  .side-text {
    justify-self: start;
    align-self: center;
    max-width: 300px;
  }
}

@media #{$mq-760-down} {
  .work-item {
    display: block;

    img {
      width: 100%;
      max-width: 400px;
      height: auto;
    }
  }
}