.top-filler {
  position: absolute;
  right: -50px;
  top: -52px;
  z-index: -99;
}

@media #{$mq-760-down} {
  .top-filler {
    display: none;
  }
}

// Nav
.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;

  ul li {
    display: inline;

    a {
      margin-left: 100px;
    }
  }
}

@media #{$mq-760-down} {
  .navigation {
    display: none;
  }
}